@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
html{
  scroll-behavior: auto;
  -ms-scroll-snap-type: y mandatory;
  scroll-snap-type: y mandatory;
  background-color: #e0e0e0;
}

::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-thumb {
  background-image: linear-gradient(to right top, #faa40f, #f99805, #f98b00, #f87e00, #f67100);
  outline: 1px solid slategrey;
  max-width: 3px;
}


body, html {
  background-color: pink;
  height: 100%;
  font-family: Verdana, Geneva, sans-serif
}

.connectionStatus {
  padding-top: 20px;
  margin-left: 5%;
  font-size: 1.5em;
}

.subscriber {
  margin-left: 10%;
}

.publisher {
  float: right;
}

#error {
  color: red;
}

.chat {
  flex: .65 1;
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.chat__header {
  padding: 20px;
  display: flex;
  align-items: center;
  border-bottom: 1px solid lightgray;
}

.chat__headerInfo {
  flex: 1 1;
  padding-left: 15px;
}

.chat__headerInfo>h3 {
  font-weight: 500;
  margin-bottom: 3px;
}

.chat__headerInfo>p {
  color: gray;
}

.chat__headerRight {
  display: flex;
  min-width: 100px;
  justify-content: space-between;
}

.chat__body {
  flex: 1 1;
  background-repeat: repeat;
  background-position: center;
  padding: 30px;
  overflow: scroll;
  overflow-y: auto;
}

.chat__body::-webkit-scrollbar {
  display: none;
}

.chat__name {
  position: absolute;
  top: -15px;
  font-weight: 800;
  font-size: x-small;
}

.chat__receiver {
  margin-left: auto;
  background-color: #f7902f !important;
}

.chat__message {
  overflow-y: auto;
  position: relative;
  font-size: 16px;
  padding: 10px;
  background-color: rgb(235, 235, 235);
  border-radius: 10px;
  margin-bottom: 30px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 1.125em 1.5em;
  border-radius: 1rem;
  max-width: 30em;
}

.chat__timestamp {
  margin-left: 10px;
  font-size: xx-small;
}

.chat__footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 62px;
  border-top: 10x solid lightgray;
}

.chat__footer>form {
  flex: 1 1;
  display: flex;
}

.chat__messageText {
  word-wrap: break-word;
}

.chat__footer>form>input {
  flex: 1 1;
  border-radius: 30px;
  padding: 10px;
  border: none;
}

.chat__footer>form>button {
  border: none;
  cursor: pointer;
}

.chat__footer>.MuiSvgIcon-root {
  padding: 10px;
}

.chat__emojiPicker {
  z-index: 99;
  bottom: 120px;
  position: fixed;
}
::-webkit-scrollbar {
  width: 2px;
  height: 1px;
}
html,
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

.App-header {
  background-color: #222;
  height: 40px;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
}

.App-header h1 {
  font-size: 16px;
  font-weight: 200;
}

.App-logo {
  height: 60%;
  width: auto;
}

.App-main {
  position: relative;
  width: 75vw;
  height: calc(75vw * .6);
  margin: 10px auto;
  border: 1px solid lightblue;
}

.App-control-container {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
}

.App-control-container.hidden {
  display: none;
}

.App-control-container .ots-video-control {
  width: 50px;
  height: 50px;
  margin: 20px 0 !important;
  border: 2px solid white;
  border-radius: 50%;
  background-position: center;
  background-color: rgba(27, 134, 144, 0.4);
  background-color: lightgrey;
  background-repeat: no-repeat;
  cursor: pointer;
}

.App-control-container .ots-video-control.audio {
  background-image: url(https://assets.tokbox.com/solutions/images/icon-mic.png);
}

.App-control-container .ots-video-control.audio:hover,
.App-control-container .ots-video-control.audio.muted {
  background-image: url(https://assets.tokbox.com/solutions/images/icon-muted-mic.png);
}

.App-control-container .ots-video-control.video {
  background-image: url(https://assets.tokbox.com/solutions/images/icon-video.png);
}

.App-control-container .ots-video-control.video.muted {
  background-image: url(https://assets.tokbox.com/solutions/images/icon-no-video.png);
}

.App-control-container .ots-video-control.end-call {
  background-image: url(https://assets.tokbox.com/solutions/images/icon-hang-up.png);
  background-color: red;
}

.App-video-container {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.App-mask {
  width: 100%;
  height: 100%;
  position: relative;
  color: white;
  background: rgba(27, 134, 144, 0.4);
  display: flex;
  justify-content: center;
  align-items: center;
}

.App-mask .react-spinner {
  position: absolute;
}

.App-mask .message {
  font-weight: 200;
}

.App-mask .message.with-spinner {
  position: absolute;
  top: 55%;
}

.App-mask .message.button {
  border: 1px solid white;
  padding: 20px 40px;
  border-radius: 6px;
  background-color: Transparent;
  color: white;
  font-family: sans-serif;
  font-size: medium;
}

.App-mask .message.button:focus:active,  .message.button:focus, .message.button:active {
  background-image: none;
  outline: none;
  box-shadow: none;
}

.App-video-container .video-container {
  width: 100%;
  height: 100%;
  display: flex;
}

.App-video-container .video-container.small {
  position: absolute;
  top: 20px;
  right: 20px;
  width: 160px;
  height: 96px;
  border: 1px solid #fcba00;
  z-index: 2;
}

.App-video-container .video-container.small.left {
  left: 20px;
  border: 1px solid #00fcc2;
}

.App-video-container .video-container.hidden {
  display: none;
}

.App-video-container .video-container.active-gt2 {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  height: 100%;
  width: 100%;
}

.App-video-container .video-container.active-gt2.hidden {
  display: none;
}

.App-video-container .video-container.active-gt2 .OT_subscriber {
  margin: 0;
  padding: 0;
  display: inline-block;
  font-size: 0;
  flex-basis: 50%;
  box-sizing: border-box;
  width: auto!important;
  height: auto!important;
}

.App-video-container .video-container.active-gt2.active-odd .OT_subscriber:first-child {
  flex-basis: 100%;
}
